/* App.module.css */
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: #222;
  }
  
  .App {
    text-align: center;
    color: white;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 50px;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  