.button {
  margin: 5px;
  color: white;
  background-color: #282c34;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border: 2px solid #61dafb;
  transition: border 0.3s;
}

.button:hover {
  border: 2px solid #fbda61;
}

.active-button {
  margin: 5px;
  color: white;
  background-color: #282c34;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border: 2px solid #fbda61;
}

.disabled-button {
  margin: 5px;
  margin-top: 20px;
  color: white;
  background-color: #282c34;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: default;
  border: 2px solid #7e7e7e;
}
